html,body{
    width: 100%;
    height: 100%;
}
.bg-gradient{
        background-image: linear-gradient(112.84deg,#5592ea .33%,#7fe1fb 38.23%,#fe9d4c 65.22%,#c1abff);
        width: 100%;
}
.h-100{
    height: 100vh;
}
.nav{
    display: grid;
    grid-template-columns: 20% 50% 30%;
    padding:20px;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.first-section{
    
}
.third-section{
    float: right;
display: flex;
gap: 2%;
}
.second-section{
display: flex;
gap: 5%;
}
.styled-text{
    display: flex;
    color: white;
    font-size: 3rem;
}
.text-capitalize{
    text-transform: uppercase;
}
.letter-spacing{
    padding-left: 30px;
    letter-spacing: 3px;
}
#text-small{
    margin-top: -10px;
    font-size:x-small;
}
.button{
    border: 2px solid white;
    border-radius: 5px;
    color: white;
    text-transform: uppercase;
    padding: 10px;
    width: max-content;
}
ul{
    list-style: none;
    display: flex;
    gap: 5%;
    justify-content: space-around;
    padding: 10px;
    align-items: center;
}
ul li{
    color: white;
    /* font-weight: bold; */
    text-transform: capitalize;
    font-size:large;
}
.text-white{
    color: white;
    font-weight: 400;
    font-size: larger;
}
.connexion{
    padding: 10px;
    font-size: 15px !important;
}
.btn-bg-blur{
    text-transform: uppercase;
    background-color: rgba(255,255,255, 0.2);
    padding: 10px 2px;
    border-radius: 5px;
}
.dropdown{
    background-color: rgba(255, 255, 255, 0.2);
    padding: 10px;
    height: fit-content;
    color: white;
}
.body-section{
    margin-top: 50px;
    padding: 30px 0px 20px 10px;
    min-height: max-content !important;
    display: flex;
    flex-wrap: wrap;
}
.left-part{
    width: 50%;
    min-width: 310px;
}
.right-part{
    width: 50%;
    min-width: 310px;
}
.heading-container{
    margin: 30px 10px;
}
.body-heading{
    font-weight: bolder;
    font-size: xxx-large;
    color:white;
    margin: -10px;
}
.body-text{

}
.flex{
    margin: 20px 0px;
    display: flex;
    
    gap: 5%;
}